#patient-list .top_bar {
  background-color:#8A1065;
  height: 70px;
}
#patient-list .bottom_bar {
  background-color:#8A1065;
  height: 70px;
  margin-top: 50px;
  margin-bottom: 10px;
}

#patient-list .card {
  display:flex;
  flex-direction: row;
  width: 300px;
  height: 400px;



  margin-right: 155px;
  margin-left: 100px;
  margin-top: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
#patient-list .list-label {
  color: white;
  font-size: 40px;
  letter-spacing: 4px;
  font-weight: 600;
  margin-left: 15px;
  padding-top: 4px;
  border: none;
}

#patient-list  .card-img-top {
  width: 100% ;
  height: 100%;
  overflow: hidden;
}
#patient-list .card-body {

}
#patient-list .card-title {
  color: black;
  margin-top:100px;
}
#patient-list a:hover {
  text-decoration: none;
}
#patient-list a {
  display:inline-block;
}
#patient-list .left {
  width:1000px;
  display: flex;
  flex-direction: row;
  justify-content: center;

}
#patient-list .addpatient {
  font-size: 25px;
  width: 350px;
  margin-left: 20px;
  color: black;

}

